import { useLayoutEffect } from 'react';

export const useAnalyticsBridge = (pageId) => {
    useLayoutEffect(() => {
        // noinspection JSUnresolvedReference
        const analyticsCb = window.msa;

        if (!analyticsCb) {
            return;
        }

        analyticsCb('view', pageId);

        document.addEventListener('click', (e) => {
            /**
             * @type {Element}
             */
            let el = e.target;

            while (el) {
                if (el === document.body) {
                    return;
                }

                if (el.getAttribute('data-element') === 'custom-link') {
                    analyticsCb('click_link', pageId, { category: el.getAttribute('data-id') });

                    break;
                }

                const messenger = el.getAttribute('data-messenger') || el.getAttribute('data-product-messenger');

                if (messenger) {
                    analyticsCb('click_messenger', pageId, { category: messenger });

                    break;
                }

                el = el.parentNode;
            }
        });
    }, [pageId]);
};
